import BasicLaylout from "../../layouts/BasicLayout";
import React, { useState } from "react";
import "./LoginForm.css";
import axios from "axios";
import Form from "react-bootstrap/Form";
import $ from "jquery"; // jQuery 라이브러리 추가

const ApiTest = () => {
    const [newMember, setNewMember] = useState({
        hostNum: "",
        password: "",
        hostName: "",
        phoneNum: "",
    });

    const handleInputChange = (e) => {
        const { name, value } = e.target;
        setNewMember((prevNewMember) => ({
            ...prevNewMember,
            [name]: value,
        }));
    };

    const handleSubmit = async (e) => {
        e.preventDefault(); // 기본 폼 제출 방지

        // 회원가입 유효성 검사 코드 추가

        try {
            // 회원가입 API 호출 코드 추가

            // jQuery를 이용하여 사업자등록번호 조회 기능 추가
            var data = {
                b_no: [newMember.hostNum],
            };

            let serviceKey = "jNJwXhsGE%2BR4ssYCiosHjGYR%2FzyXPcxd0kTjAMRluKEZ7UNjAf0G37HlXVE5YGQBs5LgDaSfEdI1K%2F%2B3%2Buf1rQ%3D%3D";

            $.ajax({
                url: "https://api.odcloud.kr/api/nts-businessman/v1/status?serviceKey=" + serviceKey,
                type: "POST",
                data: JSON.stringify(data),
                dataType: "JSON",
                contentType: "application/json",
                accept: "application/json",
                success: function (result) {
                    var statusCd = result.data[0].b_stt_cd;
                    var licenseNo = result.data[0].b_no;
                    var message = "";

                    if (statusCd === "01") {
                        message = "사업자등록번호 " + licenseNo + " 는(은) 정상 사업자입니다.";
                    } else if (statusCd === "02") {
                        message = "사업자등록번호 " + licenseNo + " 는(은) 휴업 중인 사업자입니다.";
                    } else if (statusCd === "03") {
                        message = "사업자등록번호 " + licenseNo + " 는(은) 폐업한 사업자입니다.";
                    } else {
                        message = "사업자등록번호 " + licenseNo + " 는(은) 미등록된 사업자입니다.";
                    }

                    alert(message);
                },
                error: function (error) {
                    alert("에러 발생");
                    console.log("에러: ", error);
                },
            });
        } catch (error) {
            console.error("Unexpected error:", error.message);
        }
    };

    return (
        <BasicLaylout>
            <div className="loginBox">
                <h1>
                    <b>회원가입</b>
                </h1>
                <Form onSubmit={handleSubmit}>
                    <span className="formName">사업자번호</span>
                    <input className="formInfo" type="text" placeholder="사업자번호를 입력해주세요" name="hostNum" onChange={handleInputChange} value={newMember.hostNum} />

                    <span className="formName">비밀번호</span>
                    <input className="formInfo" type="password" placeholder="비밀번호를 입력해주세요" name="password" onChange={handleInputChange} value={newMember.password} />

                    <span className="formName">상호명</span>
                    <input className="formInfo" type="text" placeholder="상호명을 입력해주세요" name="hostName" onChange={handleInputChange} value={newMember.hostName} />

                    <span className="formName">전화번호</span>
                    <input className="formInfo" type="text" placeholder="전화번호를 입력해주세요" name="phoneNum" onChange={handleInputChange} value={newMember.phoneNum} />

                    <div className="loginBtn">
                        <button type="submit">회원가입</button>
                    </div>
                </Form>
            </div>
        </BasicLaylout>
    );
};

export default ApiTest;